<template>
    <div class="classIfiCation">
        <van-nav-bar
            title="宠物社区"
            right-text="发帖"
            @click-right="goReleasePost"
        />
        <div class="pets_list">
            <van-tabs v-model="active" @change="toggtab">
                <van-tab :title="it.title" class="pets_list_tab" v-for="(it,ind) in showAllPostData" :key="ind" >
                    <div class="pets_list_tab_1" v-for="(item,index) in it.showPostData" :key="index" @click="goPostText(item,index)">
                        <div class="pets_list_tab_1Left fl">
                             <img :src="item.userHeadPortrait"/>
                        </div>
                        <div class="pets_list_tab_1Right fl">
                           <div class="item_titleAndContent">
                               <div class="item_title on-title">{{item.title}}</div>
                               <div class="item_Content">
                                   {{item.userContent}}
                               </div>
                              
                               <div class="item_watch on-title">
                                    <!-- 评价人数 -->
                                   <div class="item_watch_1 fl">
                                       <van-icon name="comment-o" size="20" class="fl"/>
                                       <span>{{item.commentNumberContent.length}}</span>
                                   </div>
                                   <!-- 收藏人数 -->
                                   <div class="item_watch_1 fl">
                                       <van-icon name="star-o" size="20" class="fl"/>
                                       <span>{{item.CollectionNumber}}</span>
                                   </div>
                               </div>
                               <van-icon name="arrow" color="#1989fa" class="item_watch_icon" size="40" />
                           </div>
                        </div>
                    </div>
                    
                </van-tab>
                <!-- <van-tab title="最新">最新</van-tab>
                <van-tab title="我的贴子">我的帖子</van-tab> -->
            </van-tabs>
        </div>
    </div>
</template>

<script>
import '../../assets/css/ClassIfiCation.less'
import {createNamespacedHelpers} from 'vuex'

const {mapState,mapMutations} = createNamespacedHelpers('homeModules')
    export default {
        data(){
            return {
                active: 0,
                showAllPostData: [
                    {showPostData:[],title: '热帖'},
                    {showPostData:[],title: '最新'},
                    {showPostData:[],title: '我的帖子'}
                ]
            }
        },
        computed: {
            ...mapState(['communityData','tabClassData'])
        },
        created(){
            this.toggtab(this.tabClassData);
        },
        methods:{
            ...mapMutations(['toggTabClassData']),
            // getCommunityData(){
            //     this.showAllPostData[0].showHotPostData=this.communityData.hotPostData;
            // },
            goPostText(item,index){

                this.$router.push({name: 'PostText', query: {type: item.typeData,num:index}})
            },
            toggtab(index){
                    //获取本地存储数据
                let localSData=localStorage.getItem('pestData');
                localSData=JSON.parse(localSData);
                
                if(index==2){
                    
                    this.showAllPostData[index].showPostData=localSData!=null?localSData["communityData"].myPostData:this.communityData.myPostData;
                }else if(index==1){
                    
                     this.showAllPostData[index].showPostData=localSData!=null?localSData["communityData"].newPostData:this.communityData.newPostData;
                    // this.showAllPostData[index].showPostData=this.communityData.newPostData;
                }else{
                     this.showAllPostData[index].showPostData=localSData!=null?localSData["communityData"].hotPostData:this.communityData.hotPostData;
                    // this.showAllPostData[0].showPostData=this.communityData.hotPostData;
                }
                this.toggTabClassData(index);
                this.active=this.tabClassData;
            },
            goReleasePost(){
                 if(localStorage.getItem('__Tk')==null){
                            this.$dialog.confirm({
                                title: '请先登录再进行发布帖子',
                                message: '是否立刻进行登录',
                            })
                            .then(() => {
                                this.$router.push({name: "Login"})
                            })
                            .catch(() => {
                                
                            });
                }else{
                     this.$router.push({name: 'ReleasePost'});
                }
            }
        }
    }
</script>